.hero {
    padding-left: 64px;
    padding-bottom: 100px;
    background-image: url(https://img.freepik.com/fotos-gratis/um-delicioso-bolo-de-aniversario-com-baloes_23-2151010637.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .container {
        position: relative;
    }

    &::before{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        content: '';
        opacity: .7;
    }

    &__text {
        margin-top: 64px;
        margin-bottom: 64px;
        font-size: 30px;
        line-height: 56px;
        font-weight: 800;
        text-align: center;
    }

    &__logo {
        margin: 0 auto;
        height: 80px;
        display: block;
    }

    @media (max-width: 640px) {
        padding: 40px 0;
        
        &__logo {
            height: 35px;
        }

        &__text {
            font-size: 20px;
            line-height: 24px;
            margin: 24px 0;
        }
    }
}